@import "../styles/main.scss";
@import "../styles/variable.scss";
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.home {
  background-color: $tint;
  .homePage {
    padding-left: 2%;
    padding-right: 2%;
    padding-top: 3%;
    padding-bottom: 3%;
    min-height: 99vh;
  }
  .menu-container {
    height: 50px;
    padding-right: 5%;
    @media (max-width: $screen-sm) {
      height: 40px !important;
      padding-right: 7% !important;
    }
    @media (max-width: $screen-sm) {
      padding-right: 12% !important;
    }
  }
  .navigation-item {
    font-size: 30px;
    font-family: "OhNow", sans-serif;
    padding-top: 8pt;
    @media (max-width: $screen-lg) {
      font-size: 25px !important;
    }
  }
  .navigation-decorator {
    background-color: $highlight;
    color: $highlight;
  }
  .logo {
    height: 30pt;
    width: 35px;
  }
  .center-image {
    max-width: 400px;
    min-width: 200px;
    padding-top: 5%;
    @media (max-width: $screen-xs) {
      max-width: 300px;
      min-width: 100px;
    }
  }
  .fixed-icon-container {
    position: fixed;
    z-index: 4000 !important;
  }
  .github {
    bottom: 105px;
    @media (max-width: $screen-sm) {
      bottom: 65px;
    }
  }
  .linkedin {
    bottom: 45px;
    @media (max-width: $screen-sm) {
      bottom: 20px;
    }
  }
  .fixed-icon {
    max-width: 50px;
    min-width: 50px;
    max-height: 75px;
    height: auto;
    @media (max-width: $screen-sm) {
      max-width: 40px;
      min-width: 40px;
    }
  }
  .center-name {
    min-height: 75vh;
  }
}
.remove-all-padding {
  padding: 0 !important;
}
.remove-all-margin {
  margin: 0 !important;
}
.page-title {
  font-size: 45px;
  margin: 0;
  font-family: "OhNow", sans-serif;
  @media (max-width: $screen-sm) {
    font-size: 35px;
  }
}
.project-page {
  padding-bottom: 2%;
  .left-project {
    height: 90vh;
    min-height: 600px;
    @media (max-width: $screen-md) {
      min-height: 900px;
    }
    @media (max-width: $screen-sm) {
      min-height: 900px;
    }
  }
  .right-project {
    height: 45vh;
    min-height: 300px;
    @media (max-width: $screen-md) {
      min-height: 450px;
    }
    @media (max-width: $screen-sm) {
      min-height: 600px;
    }
  }
  .fullSize {
    height: 100%;
    width: 100%;
  }
}
$base: 0.6rem;
.chevron {
  position: absolute;
  width: $base * 3.5;
  height: $base * 0.8;
  opacity: 0;
  transform: scale(0.3);
  animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
  animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move-chevron 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: "";
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: #FBFBF2;
}

.chevron:before {
  left: 0;
  transform: skewY(30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skewY(-30deg);
}

@keyframes move-chevron {
  25% {
    opacity: 1;
  }
  33.3% {
    opacity: 1;
    transform: translateY($base * 3.8);
  }
  66.6% {
    opacity: 1;
    transform: translateY($base * 5.2);
  }
  100% {
    opacity: 0;
    transform: translateY($base * 8) scale(0.5);
  }
}
.title-container {
  height: 10vh;
  min-height: 75px;
}
.about-page {
  min-height: 100vh;
  .divider {
    height: 1vh;
  }
}
.contact-page {
  margin-top: 50px;
  .contact-page-title {
    font-size: 35px;
    margin: 0;
    font-family: "OhNow", sans-serif;
    @media (max-width: $screen-xs) {
      font-size: 30px;
    }
  }
}
.cursor-pointer {
  cursor: pointer;
}
.contact-subtitle {
  font-family: "Coves-Light", sans-serif;
  font-size: 25px;
}
.subtitle-container {
  padding-bottom: 10px;
  margin-top: -20px;
}
