@import "@/styles/main.scss";























































































@import "../styles/main.scss";
@import "../styles/variable.scss";
.container {
  margin-top: 5vh;
  margin-bottom: 5vh;
  height: 250px;
  width: 250px;
}
.text-container {
  padding-top: 5vh;
  padding-bottom: 5vh;
}
.remove-all-margin {
  margin: 0px;
}
.fullSize {
  height: 100%;
  width: auto;
}
.fullWidth {
  width: 100vw;
}
.about-section-title {
  font-family: "Coves-Light", sans-serif;
  font-size: 35px;
}
.about-section-description {
  color: white;
  font-family: "Coves-Light", sans-serif;
  font-size: 22px;
}
.about-section-container {
  min-height: 45vh;
  background-repeat: no-repeat;
  background-size: cover;
}
